@mixin font($size: $font-size_base,  $weight: $font-weight_base, $color: $color_base,
  $line-height: $line-height_base) {
  font-size: $size;
  font-weight: $weight;
  line-height: $line-height;
  color: $color;
}

@mixin link_none {
  text-decoration: none;
}

@mixin visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin upper {
  text-transform: uppercase;
}
